import axios from 'axios';

const apiUrl = 'https://arloid-api.herokuapp.com/api/';

class ApiService {
    submitForm(firstName,lastName,companyName,email,mobileNumber,position,dateOfMeeting) {
        return axios.post(apiUrl + 'submit-form', {
            firstName: firstName,
            lastName: lastName,
            email: email,
            companyName: companyName,
            position: position,
            mobileNumber: mobileNumber,
            dateOfMeeting: dateOfMeeting
        },
        {
            headers: {
            'Access-Control-Allow-Origin': '*',
            'Content-Type': 'application/json',
            },
        }
        )
    }
    readData () {
        return axios.get(apiUrl + "section", {
        }, 
        {
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Content-Type': 'application/json',
                },

        })  ;
       
    }
}

export default new ApiService();