<template>
  <v-app>
    <v-main v-if="!loader">
      <HeaderSection @scrollTo="scrollToPosition" />
      <div v-for="section in sections" :key="section.id">
        <component v-bind:is="section.slug" :data="section" :ref="section.slug"></component>
      </div>
      <FooterSection />
    </v-main>
    <v-main v-else>
      <LoaderContainer />
    </v-main>
  </v-app>
</template>

<script>
import ApiService from "./service.js";
import DescriptionSection from "./components/DescriptionSection.vue";
import BenefitSection from "./components/BenefitSection.vue";
import StepsSection from "./components/StepsSection.vue";
import IntroductionSection from "./components/IntroductionSection.vue";
import HappyCasesSection from "./components/HappyCasesSection.vue";
import RequestDemo from "./components/SubmitSection.vue";
import ServicesSection from "./components/ServicesSection.vue";
import FaqSection from "./components/FaqSection.vue";
import SubmitSection from "./components/SubmitSection.vue";
import DemoSection from "./components/RequestDemo.vue";
import FooterSection from "./components/FooterSection.vue";
import HeaderSection from "./components/HeaderSection.vue";
import LoaderContainer from "./components/LoaderContainer.vue";

export default {
  name: "App",
  data() {
    return {
      sections: [],
      loader: true,
    };
  },
  mounted() {
    this.readData().then(() => {
      setTimeout(() => {
        this.loader = false;
      }, 3000);
    });
  },

  methods: {
    async readData() {
      ApiService.readData().then((response) => {
        this.sections = response.data;
      });
    },

    scrollToPosition(value) {
      var element = this.$refs[value][0].$el;
      var top = element.offsetTop;

      window.scrollTo({ top: top, behavior: "smooth" });
    },
  },
  components: {
    DescriptionSection,
    BenefitSection,
    StepsSection,
    IntroductionSection,
    HappyCasesSection,
    RequestDemo,
    ServicesSection,
    FaqSection,
    SubmitSection,
    DemoSection,
    HeaderSection,
    FooterSection,
    LoaderContainer,
  },
};
</script>
<style>
@import url("https://fonts.googleapis.com/css?family=Nunito");
