<template>
  <v-container>
    <v-row  style="display: flex;
  justify-content :center"
    >
      <button 
      type="button" 
      class="button"
      ref="submitButton"
      @click="submitForm()">
      {{data.title}}
      </button>
    </v-row>
    <v-row>
      <v-text-field 
      v-model="firstName" 
      ref="firstName"
      placeholder=" First Name" 
      class="text-field" 
      :rules="[rules.required]" 
      rounded
      outlined>
      </v-text-field>
    </v-row>
    <v-row>
      <v-text-field 
      ref="lastName"
      v-model="lastName" 
      placeholder=" Last Name" 
      class="text-field" 
      :rules="[rules.required]" 
      rounded
      outlined>
      </v-text-field>
    </v-row>
    <v-row>
      <v-text-field 
      ref="companyName"
      v-model="companyName" 
      placeholder="Company Name" 
      class="text-field" 
      :rules="[rules.required]"
      rounded 
      outlined>
      </v-text-field>
    </v-row>
    <v-row>
      <v-text-field 
      ref="mobileNumber"
      v-model="mobileNumber" 
      placeholder="Mobile Number" 
      class="text-field" 
      :rules="[rules.required]"
      rounded 
      outlined>
      </v-text-field>
    </v-row>
    <v-row>
      <v-text-field 
      v-model="email" 
      ref="email"
      placeholder="Email" 
      class="text-field" 
      :rules="[rules.required]" 
      rounded 
      outlined>
      </v-text-field>
    </v-row>
    <v-row> 
      <v-col class="column" cols="12" lg="7" sm="12" md="7" xs="12" order-xs="2">
        <v-text-field 
        ref="position"
        v-model="position" 
        placeholder="Position" 
        class="text-field" 
        rounded 
        outlined>
        </v-text-field>
      </v-col>
      <v-col cols="12" lg="5" sm="12" md="5" xs="12">
        <v-date-picker
        ref="datePicker"
        v-model="dateOfMeeting"
        color="#996aaf"
        full-width>
        </v-date-picker>
      </v-col> 
    </v-row>
  </v-container>
</template>

<script>
import ApiService from "../service.js";
export default {
  name: "SubmitSection",
  props: ["data"],
  data() {
    return {
      rules: { required: (value) => !!value }, /* verify required fields*/
      firstName: "",
      lastName: "",
      companyName: "",
      email: "",
      mobileNumber: "",
      position: "",
      dateOfMeeting: ""
    }
  },
  methods: {
    submitForm() {
      ApiService.submitForm(this.firstName, this.lastName, this.companyName, this.email, this.mobileNumber, this.position, this.dateOfMeeting)
      .then(() => {
        this.$refs["submitButton"].textContent = "Form Submitted!";
        this.$refs["firstName"].reset();
        this.$refs["lastName"].reset();
        this.$refs["email"].reset();
        this.$refs["companyName"].reset();
        this.$refs["mobileNumber"].reset();
        this.$refs["position"].reset();
        setTimeout( () => {
        this.$refs["submitButton"].textContent = "Request a Demo";
        }, 10000)
      }) /* refresh the page to reset form*/
    }
  }
}
</script>

