<template>
  <div class="dropdown">
    <v-container>
      <div>
        <v-row style="display: flex; justify-content: center">
          <p class="faq-title" style="text-align: justify">
            {{ data.title }}
          </p>
        </v-row>
      </div>
      <div
        class="faq"
        v-for="faq in data.features"
        :key="faq.slug"
        @click="displayFaq(faq.slug)"
      >
        <v-row>
          <v-col cols="10" class="content-title"> {{ faq.title }} </v-col>
          <v-col cols="2">
            <div v-bind:ref="faq.slug + '-icon'" class="icon-class">
              <v-icon style="color: white"> {{ icons.plus }} </v-icon>
            </div>
          </v-col>
        </v-row>
        <v-row v-bind:ref="faq.slug" style="display: none">
          <v-col cols="12" class="body">
            <ul class="ul" style="margin-bottom: 50px">
              <li
                class="list"
                v-for="description in faq.description.split('.')"
                :key="description"
              >
                {{ description }}
              </li>
            </ul>
          </v-col>
        </v-row>
      </div>
    </v-container>
  </div>
</template>
<script>
import { mdiPlus, mdiClose } from "@mdi/js";
export default {
  name: "FaqSection",
  props: ["data"],
  data: () => {
    return {
      icons: {
        plus: mdiPlus,
        close: mdiClose,
      },
    };
  },
  methods: {
    displayFaq(id) {
      let el = this.$refs[id][0].style.display;
      if (el == "none") {
        this.$refs[id][0].style.display = "block";
        this.$refs[id + "-icon"][0].classList.value =
          "icon-class rotate-active";
      } else {
        this.$refs[id][0].style.display = "none";
        this.$refs[id + "-icon"][0].classList.value = "icon-class";
      }
    },
  },
};
</script>
